module.exports = "html, body {\r\n  margin: 0;\r\n  padding: 0;\r\n  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;\r\n}\r\n\r\n/* ------------------DASHBOARD GENERAL---------------------- */\r\n\r\n.dashboard-container {\r\n  width: 100%;\r\n}\r\n\r\n.group-info {\r\n  margin-bottom: 10px;\r\n  background-color: #EF5360;\r\n  padding: 5px 15px;\r\n  color: #fff;\r\n}\r\n\r\n.group-info * {\r\n  display: inline;\r\n  margin-right: 0;\r\n  font-size: 17px;\r\n  padding-left: 0;\r\n}\r\n\r\n.dashboard-name{\r\n  font-size: 25px !important;\r\n}\r\n\r\n.project-name {\r\n  float: right;\r\n  vertical-align: middle;\r\n  color: #fff;\r\n  padding-left: 20px !important;\r\n  text-decoration: none;\r\n  padding-top: 8px;\r\n}\r\n\r\n.project-name:hover {\r\n  text-decoration: none;\r\n  color: #4bd8d4;\r\n}\r\n\r\n.small-initials {\r\n  background-color: lightgray;\r\n}\r\n\r\n.small-initials p {\r\n  padding: 0;\r\n  margin: 0;\r\n  font-size: 17px;\r\n  font-weight: 500;\r\n}\r\n\r\n.dashboard {\r\n  display: flex;\r\n  overflow-x: auto;\r\n  white-space: nowrap;\r\n  padding-top: 5px;\r\n  padding-left: 5px;\r\n  padding-right: 5px;\r\n}\r\n\r\n/* ------------------TABLES---------------------- */\r\n\r\n.table {\r\n  display: inline-flex;\r\n  flex-direction: column;\r\n  max-height: 600px;\r\n  border-radius: 5px;\r\n  margin-right: 20px;\r\n  width: 250px;\r\n  background-color: #e2e4e6;\r\n}\r\n\r\n.table-name {\r\n  display: inline-block;\r\n  margin-left: 10px;\r\n  margin-top: 10px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.dropdown-button {\r\n  height: 2rem;\r\n  font-size: 2rem;\r\n  padding: 0;\r\n  margin-right: 8px;\r\n  background-color: transparent;\r\n  color: #007bff;\r\n  border-color: transparent;\r\n}\r\n\r\n.dropdown-button:hover {\r\n  background-color: transparent;\r\n  color: white;\r\n}\r\n\r\n.dropdown-button:active {\r\n  box-shadow: none;\r\n  color: #007bff;\r\n  background-color: transparent;\r\n  border-color: transparent;\r\n}\r\n\r\n.dropdown-button:focus {\r\n  background-color: transparent;\r\n  border-color: transparent;\r\n  box-shadow: none;\r\n}\r\n\r\n/* ------------------TASKS---------------------- */\r\n\r\n.task-container {\r\n  list-style: none;\r\n  overflow-y: auto;\r\n  min-height: 15px;\r\n  margin-bottom: 0;\r\n  padding: 0;\r\n}\r\n\r\n.task {\r\n  background-color: white;\r\n  border-radius: 5px;\r\n  min-height: 65px;\r\n  padding-left: 5px;\r\n  margin-left: 8px;\r\n  margin-right: 8px;\r\n  margin-bottom: 7px;\r\n}\r\n\r\n.task-overdue {\r\n  background-color: rgba(255, 0, 0, 0.2);\r\n  border-radius: 5px;\r\n  min-height: 65px;\r\n  padding-left: 5px;\r\n  margin-left: 8px;\r\n  margin-right: 8px;\r\n  margin-bottom: 7px;\r\n}\r\n\r\n.task:hover {\r\n  background-color: rgba(255, 255, 255, 0.582);\r\n}\r\n\r\n.task-name {\r\n  margin: 0;\r\n  text-overflow: clip;\r\n  display: block;\r\n  width: 100%;\r\n}\r\n\r\n.task-input {\r\n  margin-right: 8px;\r\n  margin-left: 8px;\r\n  margin-bottom: 8px;\r\n  margin-top: 0;\r\n}\r\n\r\n.add-task-btn {\r\n  background-color: #007bff;\r\n  border-color: transparent;\r\n  min-height: 30px;\r\n  padding: 0;\r\n}\r\n\r\n.add-table-btn {\r\n  border-color: transparent;\r\n  background-color: #007bff;\r\n}\r\n\r\n/* -------------------MODAL--------------------- */\r\n\r\n.label-title h5 {\r\n  margin: 0;\r\n}\r\n\r\n.label-title {\r\n  margin-bottom: 5px;\r\n}\r\n\r\n.modal-division-container {\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.form-control {\r\n  max-height: 80px;\r\n}\r\n\r\n/* ****MEMBER INITIALS**** */\r\n\r\n.initials-container {\r\n  position: relative;\r\n  display: inline-block;\r\n  background-color: darkslategray;\r\n  width: 34px;\r\n  height: 34px;\r\n  margin-right: 5px;\r\n}\r\n\r\n.initials-container a {\r\n  position: absolute;\r\n  color: white;   \r\n  margin-left: 19%;            \r\n  top: 50%;                         \r\n  -webkit-transform: translate(0, -50%);                         \r\n          transform: translate(0, -50%);\r\n}\r\n\r\n.member-initials {\r\n  width: 34px;\r\n}\r\n\r\n.member-initials:hover {\r\n  text-decoration: none;\r\n}\r\n\r\n.member-delete {\r\n  text-decoration: none;\r\n  text-align: center;\r\n  font-size: 20px;\r\n  width: 100%;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\n.overlay a {\r\n  width: 100%;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\n.overlay a:hover {\r\n  text-decoration: none;\r\n}\r\n\r\n.overlay {\r\n  position: absolute;\r\n  top: 0;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  height: 100%;\r\n  width: 100%;\r\n  opacity: 0;\r\n  transition: .5s ease;\r\n  background-color: #008CBA;\r\n}\r\n\r\n.initials-container:hover .overlay {\r\n  opacity: 1;\r\n}\r\n\r\n.add-member-container {\r\n  display: inline-block;\r\n  position: absolute;\r\n}\r\n\r\n.add-member {\r\n  background-color: lightgray;\r\n}\r\n\r\n.add-member:hover {\r\n  background-color: gray;\r\n}\r\n\r\n.add-member-btn {\r\n  font-size: 30px;\r\n  color: white;\r\n  font-weight: 500;\r\n  padding-bottom: 5px;\r\n}\r\n\r\n.add-member-btn::after {\r\n  display: none;\r\n}\r\n\r\n.members-dropdown {\r\n  margin-top: -7px;\r\n}\r\n\r\n/* ****TASK DESCRIPTION**** */\r\n\r\n.task-description:hover {\r\n  background-color: rgba(161, 160, 160, 0.459);\r\n}\r\n\r\n.description-btn-container {\r\n  margin-top: 6px;\r\n}\r\n\r\n.close-description-btn {\r\n  position: absolute;\r\n  float: none;\r\n  margin-left: 10px;\r\n  font-size: 2rem;\r\n  font-weight: 600;\r\n}\r\n\r\n/* ****TASK COMMENT TEXTBOX**** */\r\n\r\n.comment-textbox:focus {\r\n  outline: none;\r\n  box-shadow: none;\r\n  border: 1px solid #ced4da;\r\n}\r\n\r\n.comment-btn-container {\r\n  margin-top: -10px;\r\n}\r\n\r\n.add-comment-btn:disabled {\r\n  background-color: lightgray;\r\n  border-color: lightgray;\r\n}\r\n\r\n/* ****TASK MEMBERS COMMENTS**** */\r\n\r\n.comment-container {\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.comment-data-container {\r\n  display: flex;\r\n}\r\n\r\n.member-comment {\r\n  display: inline-block;\r\n  flex-grow: 1;\r\n  word-break: break-all;\r\n}\r\n\r\n.member-comment p {\r\n  margin-bottom: 0;\r\n}\r\n\r\n.confirmation-modal {\r\n  padding: 1rem;\r\n}\r\n\r\n.confirmation-options {\r\n  float: right;\r\n}\r\n\r\n.confirmation-options * {\r\n  margin-left: 25px;\r\n}\r\n\r\n/* ------------------EFFECTS---------------------- */\r\n\r\n/* Shadow */\r\n\r\n.hvr-shadow {\r\n  display: inline-block;\r\n  vertical-align: middle;\r\n  -webkit-transform: perspective(1px) translateZ(0);\r\n  transform: perspective(1px) translateZ(0);\r\n  box-shadow: 0 0 1px rgba(0, 0, 0, 0);\r\n  transition-duration: 0.3s;\r\n  transition-property: box-shadow;\r\n}\r\n\r\n.hvr-shadow:focus, .hvr-shadow:active {\r\n  box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);\r\n}\r\n\r\n/* Back Pulse */\r\n\r\n@-webkit-keyframes hvr-back-pulse {\r\n  50% {\r\n    background-color: rgba(32, 152, 209, 0.75);\r\n  }\r\n}\r\n\r\n@keyframes hvr-back-pulse {\r\n  50% {\r\n    background-color: rgba(32, 152, 209, 0.75);\r\n  }\r\n}\r\n\r\n.hvr-back-pulse {\r\n  display: inline-block;\r\n  vertical-align: middle;\r\n  -webkit-transform: perspective(1px) translateZ(0);\r\n  transform: perspective(1px) translateZ(0);\r\n  box-shadow: 0 0 1px rgba(0, 0, 0, 0);\r\n  overflow: hidden;\r\n  transition-duration: 0.5s;\r\n  transition-property: color, background-color;\r\n}\r\n\r\n.hvr-back-pulse:hover, .hvr-back-pulse:focus, .hvr-back-pulse:active {\r\n  -webkit-animation-name: hvr-back-pulse;\r\n  animation-name: hvr-back-pulse;\r\n  -webkit-animation-duration: 1s;\r\n  animation-duration: 1s;\r\n  -webkit-animation-delay: 0.5s;\r\n  animation-delay: 0.5s;\r\n  -webkit-animation-timing-function: linear;\r\n  animation-timing-function: linear;\r\n  -webkit-animation-iteration-count: infinite;\r\n  animation-iteration-count: infinite;\r\n  background-color: #2098D1;\r\n  background-color: #2098d1;\r\n  color: white;\r\n}\r\n\r\n/* Glow */\r\n\r\n.hvr-glow {\r\n  display: inline-block;\r\n  vertical-align: middle;\r\n  -webkit-transform: perspective(1px) translateZ(0);\r\n  transform: perspective(1px) translateZ(0);\r\n  box-shadow: 0 0 1px rgba(0, 0, 0, 0);\r\n  transition-duration: 0.3s;\r\n  transition-property: box-shadow;\r\n}\r\n\r\n.hvr-glow:hover {\r\n  box-shadow: 0 0 15px rgba(0, 0, 0, 0.6);\r\n}\r\n\r\n/* in-flight clone */\r\n\r\n.gu-mirror {\r\n  position: fixed !important;\r\n  margin: 0 !important;\r\n  z-index: 9999 !important;\r\n  opacity: 0.8;\r\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=80)\";\r\n  filter: alpha(opacity=80);\r\n  pointer-events: none;\r\n}\r\n\r\n/* high-performance display:none; helper */\r\n\r\n.gu-hide {\r\n  left: -9999px !important;\r\n}\r\n\r\n/* added to mirrorContainer (default = body) while dragging */\r\n\r\n.gu-unselectable {\r\n  -webkit-user-select: none !important;\r\n  -moz-user-select: none !important;\r\n  -ms-user-select: none !important;\r\n  user-select: none !important;\r\n}\r\n\r\n/* added to the source element while its mirror is dragged */\r\n\r\n.gu-transit {\r\n  opacity: 0.2;\r\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=20)\";\r\n  filter: alpha(opacity=20);\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"